#arrow{
	position: fixed;
	top: 60px;
    left: 19px;
    z-index: 9000;
}
.bounce {
	-moz-animation: bounce 3s infinite;
	-webkit-animation: bounce 3s infinite;
	animation: bounce 3s infinite;
}

$min: 7px;
$max: 10px;

@-moz-keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    -moz-transform: translateY(0);
    transform: translateY(0);
  }
  40% {
    -moz-transform: translateY($max);
    transform: translateY($max);
  }
  60% {
    -moz-transform: translateY($min);
    transform: translateY($min);
  }
}
@-webkit-keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  40% {
    -webkit-transform: translateY($max);
    transform: translateY($max);
  }
  60% {
    -webkit-transform: translateY($min);
    transform: translateY($min);
  }
}
@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    -moz-transform: translateY(0);
    -ms-transform: translateY(0);
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  40% {
    -moz-transform: translateY($max);
    -ms-transform: translateY($max);
    -webkit-transform: translateY($max);
    transform: translateY($max);
  }
  60% {
    -moz-transform: translateY($min);
    -ms-transform: translateY($min);
    -webkit-transform: translateY($min);
    transform: translateY($min);
  }
}